* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Oswald', sans-serif;
}
html {
  scroll-behavior: smooth;
    height: 100%;
}
body {
    height: 100%;
}
header {
   height: 70px;
   background-color: #22292F;
   padding: 10px 0;
}
.menu-wrap {
   display: flex;
   justify-content: space-between;
   padding: 0 15px;
}
.logo-img {
   height: 50px;
}
.menu-icon {
   font-size: 2.4em;
   color: #ffffff;
   line-height: 50px;
}
nav {
   position: absolute;
   background-color: #3D4852;
   top: 70px;
   left: 0;
   width: 100%;
}
nav ul {
   list-style-type: none;
    max-height: 0px;
    overflow: hidden;
    transition: all 0.5s;
}
nav ul li {
   padding: 0 15px;
}
nav ul li a {
   display: inline-block;
   padding: 12px;
   color: #DAE1E7;
   text-decoration: none;
   letter-spacing: 0.05em;
    font-size: 20px;
}
@media (min-width: 768px){
nav ul li a {
        padding: inherit;
}
}
nav ul li a:hover, nav ul li a:focus {
    transition: 0.5s;
    color: #878787;
}
.maintext {
        color: white;
        font-size: 40px;
        font-family: 'Secular One', sans-serif;
